@use 'sass:map';

$scale: (
  '0750': 0.75,
  // 12px
  '0813': 0.8125,
  // 13px
  '0875': 0.875,
  // 14px
  '1000': 1,
  // 16px
  '1125': 1.125,
  // 18px
  '1250': 1.25,
  // 20px
  '1375': 1.375,
  // 22px
  '1500': 1.5,
  // 24px
  '1750': 1.75,
  // 28px
  '2000': 2,
  // 32px
  '2125': 2.125,
  // 34px
  '2250': 2.25,
  // 36px
  '2500': 2.5,
  // 40px
  '2625': 2.625,
  // 42px
  '2750': 2.75,
  // 44px
  '3000': 3,
  // 48px
  '3250': 3.25,
  // 52px
  '3500': 3.5,
  // 56px
  '3750': 3.75,
  // 60px
  '4000': 4,
  // 64px
  '4250': 4.25,
  // 68px
  '4500': 4.5,
  // 72px
  '4750': 4.75,
  // 76px
  '5000': 5 // 80px
) !default;

@function value($key) {
  @if not map.has-key($scale, $key) {
    @error 'Unknown scale value `#{$key}`.';
  }
  @return map.get($scale, $key);
}
